* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  /* text-align: center; */
}

img {
  display: block;
  max-width: 100%;
}

.task_first {
  text-align: center;
  margin-top: 300px;
}

.btn__first_task {
  font-size: 30px;
  padding: 10px;
  width: 150px;
  text-align: center;
  background-color: transparent;
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.12), 5px 5px 2px 0px rgba(0, 0, 0, 0.08),
    4px 3px 1px 0px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  margin-right: 20px;
}

.timer {
  display: flex;
  justify-content: center;
  padding: 50px;
  background-color: aquamarine;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
}

.value {
  font-size: 50px;
}
